nav{
	background: rgba(0,0,0, 0.3);
	width: max-content;
	display: flex;
	padding: 0.7rem 1.7rem;
	z-index: 2;
	transform: translateX(-50%);
	bottom: 2rem;
	gap: 0.8rem;
	border-radius: 3rem;
	backdrop-filter: blur(15px);
	position: fixed;
	left: 50%;
}

.testing span,
.testing a {
	display: flex;
	justify-content: center;
	align-items: center;
}

.testing a{
	background: transparent;
	padding: 0.9rem;
	border-radius: 50%;
	display: flex;
	color: var(--color-light);
	font-size: 1.1rem;
}

.testing span{
		visibility:collapse;
		display: none;
}

.testing a:hover+span,
span:hover {
	visibility: visible;
	transition: var(--transition);
	display: inline;
	
}

nav a:hover{
	background: var(--color-bg);

}


nav a.active{
	background: var(--color-bg);
	color: var(--color-white);
}

