header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}


/* CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}


/* Social Headers */

.HeaderSocials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.HeaderSocials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


/* ME */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 20rem;
    height: max-content;
    position: absolute;
    left: calc(50% - 9rem);
    margin-top: 4rem;
    border-radius: 20rem 20rem 0 0;
    overflow: hidden;
    padding: 1rem 1.5rem 1.5rem 1.5rem;
}

.me img {
    border-radius: 20rem 20rem 0 0;
}


/* scroll down */

.scrollDown {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* Medium media  */

@media screen and (max-width:1024px) {
    header {
        height: 80vh;
    }
}


/* small media */

@media screen and (max-width:600px) {
    header {
        height: 90vh;
    }
    .HeaderSocials,
    .scrollDown {
        display: none;
    }
}